import { CCard, CCardBody, CCardHeader, CModal, CModalBody, CModalFooter, CModalHeader, CModalTitle } from "@coreui/react";
import { useEffect, useState } from "react";
import {
  assignCyberTiplineTo,
  cyberTiplineGeneratePdf,
  deleteCyberTipline,
  getCyberTipline,
  getUserByLevel,
} from "../../api";
import { dateIndo } from "../../utils/formatter";
import ConfirmDialog from "../../components/ConfirmDialog";
import DataTable from "../../components/DataTable";
import ActionButton from "../../components/ActionButton";
import Header from "../../components/Header";
import LoadingState from "../../components/LoadingState";
import { isRole } from "../../utils/auth";
import Role from "../../constants/roles";
import fileDownload from "js-file-download";
import LoadingButton from "../../components/LoadingButton";

const CyberTipline = (props) => {
  const [data, setData] = useState({});
  const [confirmDialog, setConfirmDialog] = useState(false);
  const [toDelete, setToDelete] = useState(null);
  const [loadingApi, setLoadingApi] = useState(true);
  const [loadingDelete, setLoadingDelete] = useState(false);
  const [loadingAssign, setLoadingAssign] = useState(null);
  const [keyword, setKeyword] = useState("");
  const [totalPages, setTotalPages] = useState(0);
  const [currentPage, setCurrentPage] = useState(0);
  const [modal, setModal] = useState(null);
  const [analists, setAnalists] = useState([]);
  const [ctToAssign, setCtToAssign] = useState(null);

  const loadData = async (keyword, page) => {
    setLoadingApi(true);
    try {
      const res = await getCyberTipline(keyword, page);
      const res2 = await getUserByLevel(Role.ANALYST);
      setAnalists(res2.data.content);
      formatData(res.data.content);
      setTotalPages(res.data.totalPages);
      setCurrentPage(res.data.number + 1);
    } catch (err) {}
    setLoadingApi(false);
  };

  const parseStatus = (s) => {
    const status = ["Batal", "Sidik", "Lidik", "Selesai"];
    return <span className="badge badge-secondary">{status[s]}</span>;
  };

  const formatData = (content) => {
    let columns = [];
    if (isRole(Role.ANALYST)) {
      columns = ["CT Number", "Priority", "Status", "Suspect Screen Name", "Suspect Phone", "Incident Date", ""];
    } else {
      columns = ["CT Number", "Priority", "Status", "Suspect Screen Name", "Suspect Phone", "Incident Date", "Analyst", ""];
    }
    const rows = content.map((c) =>
      isRole(Role.ANALYST)
        ? [
            c.ctNumber,
            c.priority,
            parseStatus(c.status),
            c.suspectScreenName,
            c.suspectPhone,
            dateIndo(c.incidentDate),
            actionBtn(c),
          ]
        : [
            c.ctNumber,
            c.priority,
            parseStatus(c.status),
            c.suspectScreenName,
            c.suspectPhone,
            dateIndo(c.incidentDate),
            c?.analyzedBy?.name ?? "-",
            actionBtn(c),
          ]
    );
    setData({ rows, columns });
  };

  const downloadPdf = async (d) => {
    try {
      const res = await cyberTiplineGeneratePdf(d.id);
      fileDownload(res.data, `${d.ctNumber}.pdf`);
    } catch (err) {}
  };

  const onSearch = (keyword) => {
    setKeyword(keyword);
    loadData(keyword, 0);
  };

  const handleAssign = (d) => {
    setModal("assignment");
    setCtToAssign(d);
  };

  const actionBtn = (d) => {
    const items = [
      { text: "Edit", onClick: () => props.history.push(`/cyber-tipline/edit?id=${d.id}`) },
      { text: "Detail", onClick: () => props.history.push(`/cyber-tipline/detail?id=${d.id}`) },
      { text: "Download PDF", onClick: () => downloadPdf(d) },
      { text: "Delete", onClick: () => openConfirmDialog(d) },
    ];
    if (isRole(Role.MANAGER)) {
      items.push({ text: "Assign to", onClick: () => handleAssign(d) });
    } else if (isRole(Role.ANALYST)) {
      items.push({ text: "Report", onClick: () => props.history.push(`/cyber-tipline/report?id=${d.id}`) });
    }
    return <ActionButton items={items} />;
  };

  const handleClickAssign = async (d) => {
    setLoadingAssign(d.id);
    try {
      await assignCyberTiplineTo({ ctId: ctToAssign.id, uId: d.id });
      setModal(null);
      await loadData();
    } catch (err) {}
    setLoadingAssign(null);
  };

  const openConfirmDialog = (d) => {
    setConfirmDialog(true);
    setToDelete(d);
  };

  const handleDelete = async () => {
    setLoadingDelete(true);
    try {
      await deleteCyberTipline(toDelete.id);
      setConfirmDialog(false);
      await loadData();
    } catch (err) {}
    setLoadingDelete(false);
  };

  useEffect(() => {
    loadData();
  }, []);

  if (loadingApi) return <LoadingState />;
  return (
    <>
      <CCard>
        <CCardHeader>
          <Header
            title="Cyber Tipline"
            buttons={
              isRole(Role.OPERATOR) ? [{ onClick: () => props.history.push("/cyber-tipline/create"), text: "Tambah" }] : []
            }
          />
        </CCardHeader>
        <CCardBody>
          <DataTable
            data={data}
            search={true}
            onSearch={onSearch}
            pages={totalPages}
            currentPage={currentPage}
            onActivePageChange={(n) => loadData(keyword, n)}
          />
        </CCardBody>
      </CCard>
      <ConfirmDialog
        show={confirmDialog}
        onNegative={() => setConfirmDialog(null)}
        onPositive={handleDelete}
        btnLoading={loadingDelete}
      />
      <CModal show={modal === "assignment"} onClose={() => setModal(null)} size="lg">
        <CModalHeader>
          <CModalTitle>Analyst</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <table className="table">
            <thead>
              <tr>
                <td width="40%">Nama</td>
                <td width="40%">Email</td>
                <td width="20%"></td>
              </tr>
            </thead>
            <tbody>
              {analists.map((analyst) => (
                <tr key={analyst.id}>
                  <td>{analyst.name}</td>
                  <td>{analyst.email}</td>
                  <td>
                    {loadingAssign === analyst.id ? (
                      <LoadingButton size="sm" color="info" />
                    ) : (
                      ctToAssign?.analyzedBy === null && (
                        <button className="btn btn-info btn-sm" onClick={() => handleClickAssign(analyst)}>
                          Assign
                        </button>
                      )
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </CModalBody>
        <CModalFooter>
          <button className="btn btn-secondary" onClick={() => setModal(null)}>
            Batal
          </button>
        </CModalFooter>
      </CModal>
    </>
  );
};

export default CyberTipline;
